@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Regular"), local("Montserrat-Regular"), url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Thin"), local("Montserrat-Thin"), url("../fonts/Montserrat-Thin.woff2") format("woff2"), url("../fonts/Montserrat-Thin.woff") format("woff");
  font-weight: 100;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Semi";
  src: local("Montserrat SemiBold"), local("Montserrat-SemiBold"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
.footer {
  background-color: #404040;
  padding: 1.875rem 0;
}
@media (min-width: 640px) {
  .footer {
    padding: 3.3125rem 0 2.5625rem;
  }
}
@media (max-width: 639px) {
  .footer .schedule__body-wrap {
    transform: translateX(-19px);
  }
}
.footer__s-title {
  font-family: "Montserrat Semi", Verdana, Arial, sans-serif;
  text-align: center;
  font-size: 1.375rem;
  color: #ffffff;
  margin: 0 0 1.4375rem;
}
@media (max-width: 639px) {
  .footer__s-title {
    display: none;
  }
}
.footer__title {
  font-size: 1.5rem;
  font-weight: bold;
  color: #ffffff;
  margin: 0 0 1.4375rem;
}
.footer__title svg {
  width: 0.9375rem;
  height: 0.5rem;
  display: none;
  transition: transform 0.2s ease;
}
.active .footer__title svg {
  transform: scaleY(-1);
}
@media (max-width: 639px) {
  .footer__title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: "Montserrat Semi", Verdana, Arial, sans-serif;
    font-size: 1rem;
    margin: 0;
    padding: 0.8125rem 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }
  .footer__title svg {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .active .footer__title {
    color: #4abbf3;
  }
}
.footer__call {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.footer__call .mid-btn {
  min-width: 12.25rem;
  margin: 2.125rem auto 0;
  padding-top: 1px;
}
@media (max-width: 639px) {
  .footer__call .mid-btn {
    display: none;
  }
}
@media (max-width: 639px) {
  .footer__call {
    margin: 0 0 1.25rem;
  }
}
.footer__body {
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 640px) {
  .footer__body {
    display: grid;
    align-items: flex-start;
    grid-template-columns: 1fr 1fr;
    grid-gap: 3.125rem;
  }
}
@media (min-width: 1150px) {
  .footer__body {
    grid-template-columns: 12.25rem 14.625rem 1fr;
  }
}
@media (min-width: 1280px) {
  .footer__body {
    grid-gap: 5.8125rem;
  }
}
.footer__body > .mid-btn {
  order: -1;
  margin: 1rem 0 0;
}
@media (min-width: 640px) {
  .footer__body > .mid-btn {
    display: none;
  }
}
.footer__left {
  min-width: 12.25rem;
}
@media (max-width: 1149px) {
  .footer__left {
    max-width: 12.25rem;
    margin: 0 auto;
  }
}
@media (max-width: 639px) {
  .footer__left .logo {
    display: none;
  }
}
.footer__soc {
  margin: 1.4375rem 0 0;
}
.footer__soc-title {
  font-size: 1rem;
  color: #ffffff;
  font-weight: 400;
  text-align: center;
  margin: 0 0 0.625rem;
  display: none;
}
@media (max-width: 639px) {
  .footer__soc-title {
    display: block;
  }
}
.footer__soc-body {
  display: flex;
  justify-content: center;
  gap: 0.75rem;
}
@media (min-width: 640px) {
  .footer__soc-body {
    gap: 1.5rem;
  }
}
.footer__list {
  display: flex;
  justify-content: space-between;
  gap: 0.625rem;
}
@media (min-width: 1280px) {
  .footer__list {
    padding: 0 5% 0 0;
  }
}
@media (max-width: 1149px) {
  .footer__list {
    grid-column: 1/3;
  }
}
@media (max-width: 639px) {
  .footer__list {
    margin: 0 0 1.25rem;
    flex-direction: column;
    gap: 0;
  }
}
@media (max-width: 639px) {
  .footer__col .js-acc-block {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease-in-out, margin 0.3s ease-in-out;
  }
}
@media (max-width: 639px) {
  .footer__col.active .js-acc-block {
    margin: 1.25rem 0;
  }
}
.footer__bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  font-size: 0.75rem;
  font-weight: 300;
  color: #ffffff;
  text-align: center;
  margin: 1.0625rem 0 0;
  padding: 1.0625rem 0 0;
}
@media (min-width: 640px) {
  .footer__bottom {
    font-size: 0.875rem;
    margin: 3.5rem 0 0;
  }
}
@media (max-width: 639px) {
  .footer .phone-block .simple-link {
    display: none;
  }
}

.footer-link {
  display: flex;
  align-items: center;
  color: #ffffff;
  font-size: 1rem;
  gap: 0.625rem;
  font-weight: 400;
  line-height: 1;
}
@media (max-width: 639px) {
  .footer-link {
    padding: 0.8125rem 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }
}
.footer-link + .footer-link {
  margin: 1.25rem 0 0;
}
@media (max-width: 639px) {
  .footer-link + .footer-link {
    margin: 0;
  }
}
.footer-link__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1.5625rem;
  height: 0.875rem;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.footer-link__icon svg {
  position: absolute;
  top: 50%;
  left: 13%;
  z-index: 1;
  transform: translate(-50%, -50%);
  width: 1.5625rem;
  height: 0.5625rem;
  transition: left 0.2s ease;
}
.footer-link__text {
  transform: translateX(-5px);
  transition: transform 0.2s ease;
}
.footer-link:hover {
  color: #4abbf3;
}
.footer-link:hover svg {
  left: 45%;
}
.footer-link:hover .footer-link__text {
  transform: translateX(4px);
}
.footer-link--mm {
  color: #202454;
  padding: 0.8125rem 0;
  border-bottom: 1px solid #dbebfc;
  margin: 0;
}
.footer-link--mm + .footer-link {
  margin: 0;
}
.footer-link--mm .footer-link__text {
  transform: translateX(-12px);
}
.footer-link--mm:hover {
  color: #4abbf3;
}
.footer-link--mm:hover .footer-link__text {
  transform: translateX(4px);
}

.soc-link {
  display: block;
  color: #4abbf3;
}
.soc-link--big {
  width: 2rem;
  height: 2rem;
}
@media (min-width: 640px) {
  .soc-link--big {
    width: 2.625rem;
    height: 2.625rem;
  }
}
.soc-link--mid {
  width: 2rem;
  height: 2rem;
}
.soc-link svg {
  width: 100%;
  height: 100%;
}
.soc-link:hover {
  color: #f0f6fd;
}