.micromodal-slide {
	opacity: 0;
	visibility: hidden;
}

.micromodal-slide.is-open {
	opacity: 1;
	visibility: visible;
}


.micromodal-slide[aria-hidden="false"] .modal__overlay {
	opacity: 1;
	visibility: visible;
	transform: translateY(0);
	transition: transform 0.2s cubic-bezier(0.0, 0.0, 0.2, 1),  opacity 0.5s ease, visibility 0.5s ease;
}

.micromodal-slide[aria-hidden="true"] .modal__overlay {
	opacity: 0;
	visibility: hidden;
	transition: transform 0.2s cubic-bezier(0.0, 0.0, 0.2, 1),  opacity 0.5s ease, visibility 0.5s ease;

	@media(max-width: 744px) {
		transform: translateY(5%);
	}
}

.micromodal-slide[aria-hidden="false"] .modal__container {
	transform: translateY(0);
	transition: transform 0.2s cubic-bezier(0.0, 0.0, 0.2, 1),  opacity 0.5s ease, visibility 0.5s ease;
}

.micromodal-slide[aria-hidden="true"] .modal__container {
	transform: translateY(0);
	transition: transform 0.2s cubic-bezier(0.0, 0.0, 0.2, 1),  opacity 0.5s ease, visibility 0.5s ease;
}

.micromodal-slide .modal__container,
.micromodal-slide .modal__overlay {
	will-change: transform;
}

.basket-popup {
	width: 100%;
	height: 94%;
	position: relative;
	max-width: rem(1280);
	margin: 0 auto;
	padding: 0 rem(40) rem(40);
	background-color: #fff;
	overflow-x: hidden;

	@media(max-width: 991px) {
		height: 100%;
		padding: 0 rem(16) rem(40);
	}

	@media(max-width: 560px) {
		padding: 0 rem(16) 0;
	}

	&--empty {
		max-width: rem(724);
		max-height: rem(514);
		padding: 0;

		@media(max-width: 744px) {
			max-height: 100%;
		}
	}


	&__body {
		width: 100%;
		padding: rem(50) 0 0;

		.basket-popup--empty & {
			display: none;
		}
	}

	&__bottom-left {
		display: flex;
		flex-direction: column;
		gap: rem(20);
	}

	.basket-title {
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 0 rem(12);
		gap: rem(7);

		&__text {
			font-size: rem(24);
			font-weight: bold;
			line-height: 1;
			color: $colorBlueDark;
		}

		&__count {
			display: flex;
			justify-content: center;
			align-items: center;
			min-width: rem(56);
			height: rem(56);
			border: 1px solid #4caf50;
			border-radius: 100%;
			font-size: rem(24);
			font-weight: bold;
			line-height: 1;
			color: $colorBlueDark;
		}
	}

	&__bottom {
		margin: rem(23) 0 0;

		@media(min-width: 860px) {
			display: grid;
			align-items: flex-start;
			grid-template-columns: repeat(2, 1fr);
			grid-gap: rem(20);
		}

		@media(min-width: 992px) {
			grid-template-columns: 1.28fr 1fr;
			grid-gap: rem(50);
		}
	}

	&__price {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		margin: rem(14) 0 0;
		gap: rem(13);
	}

	&__price-item {
		display: flex;

		span {
			display: block;
			font-size: rem(14);
			color: $colorBlueDark;

			&:first-child {
				text-align: left;
				font-weight: 400;
				min-width: rem(95);
			}

			&:nth-child(2) {
				font-weight: bold;
				text-align: right;
			}
		}
	}

	&__btns {
		display: flex;
		flex-direction: column;
		margin: rem(22) 0 0;
		gap: rem(11);

		.mid-btn {
			width: 100%;
			padding: 1px 0 0;
		}

		@media(max-width: 560px) {
			display: none;
		}
	}

	&__mob-btns {
		display: flex;
		flex-direction: column;
		margin: 0 0 0 -16px;
		gap: rem(11);
		position: sticky;
		z-index: 2;
		padding: rem(16);
		bottom: 0;
		width: calc(100% + 32px);
		background-color: #fff;

		@media(min-width: 561px) {
			display: none;
		}
	}

	&__checkout {

		@media(max-width: 991px) {
			margin: rem(16) 0 0;
		}
	}

	&::-webkit-scrollbar-track
	{
		border-radius: 20px;
		background-color: #fff;
		width: 8px;
		border: 1px solid #dbebfc;
	}

	&::-webkit-scrollbar
	{
		width: 8px;
		background-color: #fff;
		border: 1px solid #dbebfc;
	}

	&::-webkit-scrollbar-thumb
	{
		border-radius: 20px;
		width: 2px;
		background-color: #dbebfc;
	}
}

.empty-body {
	display: flex;
	flex-direction: column;
	width: 100%;

	&__title {
		font-size: rem(12);
		color: $colorBlueDark;
		text-align: center;
