		height: 94%;
		padding: rem(21) rem(12);
		align-items: flex-start;
		justify-content: flex-start;
	}

	&.show {
		display: flex;
	}

	&__title {
		font-size: rem(14);
		font-weight: 400;
		color: $colorBlueDark;

		@media(min-width: 992px) {
			text-align: center;
		}

		@media(max-width: 991px) {
			transform: translateY(16px);
		}
	}

	&__btns {
		width: 100%;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: rem(20);
		margin: rem(57) 0 0;

		@media(min-width: 640px) {
			max-width: rem(292);
			gap: rem(10);
			margin: 0 auto;
		}
	}

	&__yes {
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		font-family: $secFont;
		font-size: rem(14);
		color: #4CAF50;
		width: 100%;
		height: rem(33);
		border-radius: rem(12);
		border: 1px solid #4caf50;
		background-color: #fff;
		transition: background-color 0.2s ease, color 0.2s ease;
		position: relative;

		&::after {
			content: '';
			display: block;
			width: 100%;
			height: 100%;
			position: absolute;
			border-radius: inherit;
			top: 0;
			left: 0;
			z-index: 1;
			border: 1px solid $colorGreenLight;
			box-shadow: 0 rem(2) rem(4) rgba(#4CAF50, 0.4);
			opacity: 0;
			transition: opacity 0.2s ease;
