			z-index: 1;
			opacity: 0;
		}

		&.visible {
			opacity: 1;
			pointer-events: all;
		}

		svg {
			width: 75%;
			height: 75%;
		}

		&:hover {
			background-color: $colorBlueDark;
			color: #ffffff;
		}
	}

	&__input {
		width: 100%;
		height: rem(40);
		font-family: $mainFont;
		font-size: rem(14);
		color: $colorBlueDark;
		padding: 0 rem(48) 0 rem(29);
		pointer-events: all;

		@media(min-width: 992px) {
			font-size: rem(12);
			border-bottom: 1px solid #b5d8fd;
			padding: 0 rem(32) 0 rem(40);
		}

		&::placeholder {
			font-family: $mainFont;
			font-size: inherit;
			font-weight: 500;
			color: #404040;
			transition: opacity 0.2s ease;
		}

		&::-webkit-search-cancel-button {
			-webkit-appearance: none;
			appearance: none;
		}

		&:focus {
			&::placeholder {
				opacity: 0;
			}

			~ .search-block__line {

				@media(min-width: 992px) {
					transform: scaleX(1);
				}
			}
		}

		&:hover:not(:focus) {
			~ .search-block__line {
				@media(min-width: 992px) {
					transform: scaleX(0.065);
				}
			}
		}
	}
}

.search-dropdown {
	width: 100%;
	position: absolute;
	top: calc(100% - 1px);
	left: 50%;
	z-index: 1;
	opacity: 0;
	visibility: hidden;

	&.show {
		opacity: 1;
		visibility: visible;
		transform: translateY(0);

		@media(min-width: 992px) {
			transform: translateX(-50%) translate(0, 0);
		}
	}

	@media(min-width: 992px) {
		min-width: rem(400);
		transform: translate(0, 30px) translateX(-50%);
		transition: transform 0.5s ease, opacity 0.5s ease, visibility 0.5s ease;
	}

	@media(max-width: 991px) {
		position: fixed;
		width: 100%;
		top: rem(80);
		left: 0;
		z-index: 20;
		height: calc(100% - 80px);
		background-color: #fff;
		transform: translateY(5%);
		transition: transform 0.2s cubic-bezier(0.0, 0.0, 0.2, 1),  opacity 0.2s ease, visibility 0.2s ease;
	}

	@media(max-width: 744px) {
		top: rem(50);
		height: calc(100% - 50px);
	}

	&__body {
		display: flex;
		flex-direction: column;
		padding: 0;
		background-color: #fff;
		box-shadow: 0 rem(4) rem(10) rgba(#000, 0.25);
		position: relative;

		@media(max-width: 991px) {
			padding: 0 rem(20);
			height: 100%;
			box-shadow: none;
		}
	}

	&__top {
		font-family: $secFont;
		font-size: rem(18);
		color: $colorBlueDark;
		text-align: center;
		padding: rem(27) 0 rem(14);
		margin: 0 0 rem(16);

		@media(min-width: 992px) {
			display: none;
		}
	}

	&__close {
		width: rem(24);
		height: rem(24);
		position: absolute;
		top: rem(26);
		right: rem(24);
		z-index: 1;

		&::after {
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 218%;
			height: 234%;
			z-index: 1;
			opacity: 0;
		}

		img {
			width: 100%;
			height: 100%;
		}

		@media(min-width: 992px) {
			display: none;
		}
	}

	.search-block__body {
		display: none;

		@media(max-width: 991px) {
			display: flex;
		}
	}

	&__bottom {
		border-top: 1px solid #dbebfc;
	}

	&__list {
		overflow: auto;
		width: calc(100% + 10px);
		padding: 0 rem(10) 0 0;

		@media(max-width: 991px) {
			height: calc(100% - 60px);
		}

		@media(min-width: 992px) {
			max-height: rem(520);
		}

		&::-webkit-scrollbar-track
		{
			border-radius: 20px;
			background-color: #fff;
			width: 8px;
			border: 1px solid #dbebfc;
		}

		&::-webkit-scrollbar
		{
			width: 8px;
			background-color: #fff;
			border: 1px solid #dbebfc;
		}

		&::-webkit-scrollbar-thumb
		{
			border-radius: 20px;
			width: 2px;
			background-color: #dbebfc;
		}
	}

	&__result {
		padding: rem(10) rem(20) rem(44);
		display: none;

		&.has-result {
			display: block;
		}

		@media(max-width: 991px) {
			padding: 0;
			margin: rem(10) 0 0;
			height: calc(100% - 115px);
		}
	}

}

.empty-search {
	padding: rem(16) 0 rem(41);
	margin: rem(12) 0 0;

	@media(min-width: 992px) {
		padding: rem(25) rem(20) rem(41);
		margin: 0;
	}

	@media(max-width: 991px) {
		border-top: 1px solid #d6e6fd;
	}


	&__error {
		font-size: rem(14);
		line-height: 1.2;
		color: #ab0719;
		font-weight: 400;
		text-align: center;
		max-width: rem(300);
		margin: 0 auto rem(13);
		display: none;

		&.show {
			display: block;
		}
	}

	&__title {
		font-size: rem(16);
		font-weight: bold;
		color: $colorBlueDark;
		text-align: center;
	}

	&__sub-title {
		font-family: $secFont;
		font-size: rem(16);
		color: $colorBlueDark;
	}

	&__bottom {
		margin: rem(52) 0 0;
	}
}

.srch-link {
	display: flex;
	align-items: center;
	color: $colorBlueDark;
	font-size: rem(18);
	gap: rem(6);
	font-weight: 400;
	line-height: 1;
	padding: rem(12) 0 rem(12) rem(20);
	border-bottom: 1px solid #dbebfc;

	@media(max-width: 639px) {
		padding: rem(13) 0;
		border-bottom: 1px solid rgba(#fff, 0.1);
	}

	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: rem(34);
		height: rem(14);
		position: relative;
		overflow: hidden;
		flex-shrink: 0;

		svg {
			position: absolute;
			top: 50%;
			left: -11%;
			z-index: 1;
			transform: translate(-50%, -50%);
			width: rem(32);
			height: rem(10);
			transition: left 0.2s ease;
		}
	}

	&__text {
		transform: translateX(-17px);
		transition: transform 0.2s ease;
	}

	&:hover {
		color: $colorBlueLight;

