.search-cart {
	display: flex;
	align-items: center;
	gap: rem(12);
	padding: rem(9) 0;
	border-bottom: 1px solid #dbebfc;

	&:last-child {
		border: none;
	}

	@media(max-width: 991px) {
		padding: rem(10) 0;

		&:first-child {
			border-top: 1px solid #dbebfc;
		}
	}

	&__img {
		display: block;
		width: rem(80);
		height: rem(100);
		flex-shrink: 0;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__text {
		font-size: rem(14);
		line-height: 1.2;
		color: $colorBlueDark;
		display: -webkit-box;
		-webkit-line-clamp: 4;
		-webkit-box-orient: vertical;
		overflow: hidden;

		&:hover {
			color: $colorBlueLight;
		}
	}
}

.search-all {
	display: flex;
	align-items: center;
	justify-content: flex-end;
	gap: rem(10);
	color: #4abbf3;
	margin: rem(12) rem(-2) 0 auto;

	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: rem(16);
		height: rem(10);
	}

	&__text {
		font-size: rem(14);
		line-height: 1;
		position: relative;

		&::after {
			content: '';
			position: absolute;
			top: calc(100% + #{rem(2)});
			left: 0;
			width: 100%;
			height: rem(1);
			z-index: 1;
			transform: scaleX(0);
			transform-origin: left;
			transition: transform 0.2s ease;
			background-color: $colorBlueDark;
		}
	}

	&:hover {
		color: $colorBlueDark;

		.search-all__text {
			&::after {
				transform: scaleX(1);
			}
		}
	}
}


