	gap: rem(10);
	position: absolute;
	width: 100%;
	height: 100%;
	top: 50%;
	left: 50%;
	z-index: 1;
	transform: translate(-50%, -50%);

	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: rem(36);
		height: rem(36);
		transform: rotate(44deg);
		border: 1px solid $colorBlueLight;
		border-radius: rem(8);
		transition: border-color 0.2s ease;

		img {
			width: rem(30);
			height: rem(30);
			transform: rotate(-21deg);
		}
	}

	&__text {
		font-family: $secFont;
		font-size: rem(16);
		color: $colorBlueLight;
		position: relative;
		transition: color 0.2s ease;

		&::after {
			content: '';
			position: absolute;
			top: 100%;
			left: 0;
			width: 100%;
			height: rem(1);
			z-index: 1;
			transform: scaleX(1);
			transform-origin: left;
			transition: transform 0.2s ease;
			background-color: $colorBlueLight;
		}

		&::before {
			content: '';
			position: absolute;
			top: 100%;
			left: 0;
			width: 100%;
			height: rem(1);
			z-index: 2;
			transform: scaleX(0);
			transform-origin: left;
			transition: transform 0.2s ease;
			background-color: $colorBlueDark;
		}
	}

	&:hover {

		.sale-link__icon {
			border-color: $colorBlueDark;
		}

		.sale-link__text {
			color: $colorBlueDark;

			&::before {
				transform: scaleX(1);
			}
		}

	}
}

.sale-item {
	display: block;

	.swiper-slide & {
		@media(max-width: 560px) {
			width: 95%;
		}
	}

	&__img {
