.call-dropdown {
	width: rem(360);
	position: absolute;
	top: calc(100% + 14px);
	left: 50%;
	z-index: 1;
	opacity: 0;
	visibility: hidden;

	@media(min-width: 992px) {
		transform: translate(0, 30px) translateX(-50%);
		transition: transform 0.5s ease, opacity 0.5s ease, visibility 0.5s ease;
	}

	@media(max-width: 991px) {
		transform: translateY(5%);
		transition: transform 0.2s cubic-bezier(0.0, 0.0, 0.2, 1),  opacity 0.2s ease, visibility 0.2s ease;
	}

	&.show {
		opacity: 1;
		visibility: visible;
		transform: translateX(-50%) translateY(0);

		@media(max-width: 991px) {
			transform: translateY(0);
		}
	}

	&::before {
		content: '';
		display: block;
		position: absolute;
		bottom: 100%;
		left: 0;
		z-index: 1;
		width: 100%;
		height: rem(15);
	}

	@media(max-width: 991px) {
		position: fixed;
		width: 100vw;
		height: calc(100% - 50px);
		background-color: rgba(#404040, 0.8);
		backdrop-filter: blur(#{rem(50)});
		left: 0;
		top: rem(79);
		transform: translateY(5%);
	}

	@media(max-width: 744px) {
		overflow: auto;
		top: rem(50);
		background-color: #fff;
	}

	&__body {
		display: flex;
		flex-direction: column;
		padding: rem(24) rem(16) rem(44);
		background-color: #fff;
		box-shadow: 0 rem(4) rem(10) rgba(#000, 0.25);

		@media(max-width: 991px) {
			max-width: rem(360);
			margin: 0 auto;
			padding: 0 rem(16) rem(44);
		}

		@media(max-width: 744px) {
			max-width: 100%;
			box-shadow: none;
		}
	}

	&__phones-title {
		font-family: $secFont;
		font-size: rem(18);
		color: $colorBlueDark;
		text-align: center;

		@media(min-width: 992px) {
			display: none;
		}
	}

	&__phones {
		text-align: center;
		padding: rem(14) 0 rem(12);

		@media(min-width: 992px) {
			padding: rem(14) 0 rem(24);
		}

		a {
			display: block;
			font-size: rem(16);
			color: $colorBlueDark;

			&:first-child {

				@media(min-width: 992px) {
					display: none;
				}
			}

			+ a {
				margin: rem(14) 0 0;
			}

			&:nth-child(2) {
				@media(min-width: 992px) {
					margin: 0;
				}
			}

			&:hover {
				color: $colorBlueLight;
			}
		}
	}

	&__close {
		width: rem(24);
		height: rem(24);
		margin: rem(12) 0 rem(12) auto;
		position: relative;

		&::after {
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 180%;
			height: 180%;
			z-index: 1;
			opacity: 0;
		}

		@media(min-width: 992px) {
			display: none;
		}

		img {
			width: 100%;
			height: 100%;
		}
	}
}

.phone-link {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 10px;

	@media(min-width: 992px) {
		display: none;
	}

	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: rem(24);
		height: rem(24);

		img {
			width: 100%;
			height: 100%;
		}
	}

	&__text {
		font-size: rem(16);
		color: $colorBlueDark;
		line-height: 1;
		border-bottom: 1px solid $colorBlueDark;
	}
}

.chat-block {
	margin: 0 0 rem(25);

	@media(max-width: 991px) {
		margin: rem(46) 0 rem(40);
	}

	&__title {
		font-family: $secFont;
		font-size: rem(18);
		text-align: center;
		color: $colorBlueDark;
		margin: 0 0 rem(12);
	}

	&__list {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: rem(14);
	}
}

.chat-link {
	width: rem(40);
	height: rem(40);

	img {
		width: 100%;
		height: 100%;
		transition: transform 0.2s ease;
	}

	&:hover {

		img {
			transform: scale(0.95);
		}
	}
}

.map-way {
	display: flex;
	justify-content: center;
	gap: rem(12);
	margin: 0 0 rem(40);

	@media(min-width: 992px) {
		margin: 0 0 rem(25);
	}

	&__left {
		display: flex;
		align-items: center;
		gap: rem(7);

		img {
			width: rem(26);
			height: rem(26);
		}
	}

	&__text {
		font-family: $secFont;
		font-size: rem(18);
		color: $secFont;
	}

	&__links {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: rem(11);

		a {
			display: flex;
			justify-content: center;
			align-items: center;
			width: rem(36);
			height: rem(36);
			color: $colorBlueLight;

			svg {
				width: 100%;
				height: 100%;
			}

			&:hover {
				color: $colorBlueDark;
			}
		}
	}
}

.count-link {
	display: flex;
	justify-content: center;
	align-items: center;
	color: $colorBlueDark;
	position: relative;


	&--desk {
		@media(max-width: 991px) {
			display: none;
		}
	}

	&--mob {
		@media(min-width: 745px) {
			display: none;
		}
	}

	&__numb {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		line-height: 1;
		top: rem(-10);
		right: rem(-8);
		z-index: 1;
		min-width: rem(20);
		height: rem(20);
		border-radius: rem(20);
		padding: rem(1) 0 0;
		font-size: rem(10);
		font-weight: bold;
		background-color: #fde64f;
		color: $colorBlueDark;

		@media(max-width: 744px) {
			min-width: rem(16);
			height: rem(16);
			font-size: rem(8);
			top: rem(-5);
			right: rem(-4);
		}
	}

	&__icon {
		width: rem(28);
		height: rem(28);
		transition: transform 0.2s ease, color 0.2s ease;

		@media(max-width: 744px) {
			width: rem(24);
			height: rem(24);
		}
	}

	&:hover {

		@media(min-width: 992px) {
			color: $colorBlueLight;
		}
	}

	&.opened {
		color: $colorBlueLight;


		.count-link__icon {
			filter: drop-shadow(0 0 3px rgba(#4ABBF3, 0.5));
			transform: scale(1.25);
		}

		&[data-id-modal="basket-popup"] {
			.count-link__icon {
				transform: scale(1.1);
			}
		}
	}
}

.log-item {
	color: $colorBlueDark;
	transition: color 0.2s ease;

	@media(min-width: 992px) {
		margin: 0 rem(18);
	}

	@media(min-width: 1280px) {
		margin: 0 rem(24) 0 rem(33);
	}

	&__body-wrap {
		display: flex;
		align-items: center;
		gap: rem(4);
		cursor: pointer;
	}

	&__icon {
		width: rem(28);
		height: rem(28);
		transition: transform 0.2s ease;

		@media(max-width: 744px) {
			width: rem(24);
			height: rem(24);
		}

		svg {
			width: 100%;
			height: 100%;
		}

		&.opened {
			color: $colorBlueLight;
			filter: drop-shadow(0 0 3px rgba(#4ABBF3, 0.5));
			transform: scale(1.25);
		}
	}

	&__name {
		font-size: rem(12);
		line-height: 1;
		font-weight: 500;
		color: $colorBlueDark;
	}

	&__sub {
		font-size: rem(10);
		font-weight: 400;
		line-height: 1;
		color: $colorBlueDark;
	}

	&__body {
		display: none;
		flex-direction: column;
		align-items: flex-start;
		gap: rem(3);

		@media(min-width: 992px) {
			display: flex;
		}
	}

	&:hover {

		@media(min-width: 992px) {
			color: $colorBlueLight;
		}

		.acc-menu {
			@media(min-width: 992px) {
				transform: translate(0, 0);
				opacity: 1;
				visibility: visible;
			}
		}
	}
}

.js-inview {

	&[data-scale] {
		transition: all .8s ease;
		transform-origin: center;
		transform: scale(0.9);

		&.animate {
			transform: scale(1);
		}
	}

	&[data-show-y] {
		transition: all .8s ease;
		transform: translate(0, 40px);
		opacity: 0;

		&.animate {
			transform: translate(0, 0);
			opacity: 1;
		}
	}
}

.log-link {
	display: flex;
	justify-content: center;
	align-items: center;
	width: rem(24);
	height: rem(24);

	img {
		width: 100%;
		height: 100%;
		transition: transform 0.2s ease;
	}

	&:hover {

		img {
			transform: scale(1.05);
		}
	}
}

.categories-menu {
	position: absolute;
	width: 100vw;
	max-height: 71vh;
	top: 100%;
	left: 50%;
	transform: translateX(-50%) translate(0, 50px);
	background-color: #fff;
	z-index: 8;
	box-shadow: 0 rem(4) rem(4) rgba(#000, 0.12);
	padding: rem(12) 0;
	opacity: 0;
	visibility: hidden;
	transition: opacity 0.5s ease, visibility 0.5s ease, transform 0.5s ease;

	.container {
		height: 100%;
	}

	&.show {
		opacity: 1;
		visibility: visible;
		transform: translateX(-50%) translate(0, 0);
	}
}

.categories-block {
	grid-template-columns: repeat(4, 1fr);
	grid-gap: rem(20);
	width: calc(100% + 16px);
	padding: 0 rem(16) 0 0;
	height: 100%;
	overflow: auto;
	opacity: 0;
	display: none;
	transform: translate(0, 20px);
	transition: transform 0.5s ease, opacity 0.5s ease;

	&.block {
		display: grid;
	}

	&.show {
		transform: translate(0, 0);
		opacity: 1;
	}

	&::-webkit-scrollbar-track
	{
		border-radius: 20px;
		background-color: #fff;
		width: 8px;
		border: 1px solid #dbebfc;
	}

	&::-webkit-scrollbar
	{
		width: 8px;
		background-color: #fff;
		border: 1px solid #dbebfc;
	}

	&::-webkit-scrollbar-thumb
	{
		border-radius: 20px;
		width: 2px;
		background-color: #dbebfc;
	}

}

.categ-item {
	width: 100%;
	display: flex;
	align-items: center;
	gap: rem(10);
	background-color: #dbebfc;
	border-radius: rem(14);
	padding: rem(11);
	transition: background-color 0.2s ease;

	&__icon {
		width: rem(32);
		height: rem(32);
		flex-shrink: 0;

		img {
			width: 100%;
			height: 100%;
		}
	}

	&__text {
		font-size: rem(16);
		font-weight: 400;
		line-height: 1.2;
		color: $colorBlueDark;
		text-align: left;

		.categ-item--mob & {
			font-size: rem(18);
			margin: 0 rem(5) 0 0;
		}
	}

	&__arr {
		width: rem(24);
		height: rem(24);
		margin: 0 0 0 auto;
		flex-shrink: 0;
		transition: transform 0.2s ease;

		img {
			width: 100%;
			height: 100%;
		}
	}


	@media(min-width: 1280px) {
		&:hover {
			background-color: #b5d8fd;
		}

		+ .categ-list,
		+ .categ-item {
			margin: rem(10) 0 0;
		}
	}

	&--mob {
		+ .categ-item--mob {
			margin: rem(8) 0 0;
		}
	}

	.active & {
		.categ-item__arr {
			transform: scaleY(-1);
		}
	}
}

.categ-list {
	display: flex;
	flex-direction: column;
	padding: rem(12) rem(29);
	gap: rem(13);
	background-color: #f0f6fd;
	border-radius: rem(14);

	a {
		display: block;
		font-size: rem(14);
		color: $colorBlueDark;

		&:hover {
			color: $colorBlueLight;
		}
	}

	@media(min-width: 1280px) {
		+ .categ-item, {
			margin: rem(12) 0 0;
		}
	}

	&--mob {
		background-color: transparent;
		padding: 0;
		gap: rem(9);

		&.js-acc-block {
			max-height: 0;
			overflow: hidden;
			transition: max-height 0.3s ease-in-out, margin 0.3s ease-in-out;
		}

		.active & {
			margin: rem(8) 0;
		}

		a {
			border-radius: rem(13);
			background-color: #f0f6fd;
			padding: rem(12) rem(10) rem(12) rem(30);
		}
	}
}

