		justify-content: space-between;
		gap: rem(10);

		@media(min-width: 1280px) {
			padding: 0 5% 0 0;
		}

		@media(max-width: 1149px) {
			grid-column: 1/3;
		}

		@media(max-width: 639px) {
			margin: 0 0 rem(20);
			flex-direction: column;
			gap: 0;
		}
	}

	&__col {

		.js-acc-block {
			@media(max-width: 639px) {
				max-height: 0;
				overflow: hidden;
				transition: max-height 0.3s ease-in-out, margin 0.3s ease-in-out;
			}
		}

		&.active {
			@media(max-width: 639px) {
				.js-acc-block {
					margin: rem(20) 0;
				}
			}
		}
	}

	&__bottom {
		border-top: 1px solid rgba(#fff, 0.1);
		font-size: rem(12);
		font-weight: 300;
		color: #ffffff;
		text-align: center;
		margin: rem(17) 0 0;
		padding: rem(17) 0 0;

		@media(min-width: 640px) {
			font-size: rem(14);
			margin: rem(56) 0 0;
		}
	}

	@media(max-width: 639px) {
		.phone-block {
			.simple-link {
				display: none;
			}
		}
	}
}

.footer-link {
	display: flex;
	align-items: center;
	color: #ffffff;
	font-size: rem(16);
	gap: rem(10);
	font-weight: 400;
	line-height: 1;

	@media(max-width: 639px) {
		padding: rem(13) 0;
		border-bottom: 1px solid rgba(#fff, 0.1);
	}

	+ .footer-link {
		margin: rem(20) 0 0;

		@media(max-width: 639px) {
			margin: 0;
		}
	}

	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: rem(25);
		height: rem(14);
		position: relative;
		overflow: hidden;
		flex-shrink: 0;

		svg {
			position: absolute;
			top: 50%;
			left: 13%;
			z-index: 1;
			transform: translate(-50%, -50%);
			width: rem(25);
			height: rem(9);
			transition: left 0.2s ease;
		}
	}

	&__text {
		transform: translateX(-5px);
		transition: transform 0.2s ease;
	}

	&:hover {
		color: $colorBlueLight;

		svg {
			left: 45%;
		}

		.footer-link__text {
			transform: translateX(4px);
		}
	}

	&--mm {
		color: $colorBlueDark;
		padding: rem(13) 0;
		border-bottom: 1px solid #dbebfc;
		margin: 0;

		+ .footer-link {
			margin: 0;
		}

		.footer-link__text {
			transform: translateX(-12px);
		}

		&:hover {
			color: $colorBlueLight;

			.footer-link__text {
				transform: translateX(4px);
			}
		}
	}
}

.soc-link {
	display: block;
	color: $colorBlueLight;

	&--big {
		width: rem(32);
		height: rem(32);

		@media(min-width: 640px) {
			width: rem(42);
			height: rem(42);
		}
	}

	&--mid {
		width: rem(32);
		height: rem(32);
	}

	svg {
		width: 100%;
		height: 100%;
	}

	&:hover {
		color: #f0f6fd;
	}
}


